#ButtonBoard button {
	padding: 0 1vw;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: stretch;
	    align-items: stretch;
	font-size: 1.4em;
	background: transparent;
	position: relative;
	z-index: 1;
}

#ButtonBoard label, #board-left div, #board-right div {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

#ButtonBoard input[type=radio] {
    position: absolute;
    opacity: 0;
    left: -100vw;
}


#ButtonBoard input[type=radio] + label img {
	-webkit-filter: saturate(0);
	        filter: saturate(0);
	opacity: .5;
	-webkit-transition: all .14s cubic-bezier(0.51, -0.38, 0.26, 3.01);
	-o-transition: all .14s cubic-bezier(0.51, -0.38, 0.26, 3.01);
	transition: all .14s cubic-bezier(0.51, -0.38, 0.26, 3.01);
}

#ButtonBoard input[type=radio]:checked + label img {
	opacity: 1;
	-webkit-transform: scale(1.6);
	    -ms-transform: scale(1.6);
	        transform: scale(1.6);
	-webkit-filter: saturate(100%);
	        filter: saturate(100%);
	z-index: -1;
}

#ButtonBoard img {
	width: auto;
	height: 6vw;
	max-height: 10vh;
}

@media (max-width: 768px) {
	#ButtonBoard img {
		height: auto;
	}
	.points {
		height: 52px;
	}
}


