#modal-holder {
	position: fixed;
	-webkit-transition: var(--transition);
	-o-transition: var(--transition);
	transition: var(--transition);
	z-index: 11;
}

.modal {
	position: fixed;
	left: calc(50vw - 190px);
	top: calc(30vh);
	z-index: 10;
	width: 380px;
	height: auto;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-line-pack: center;
	    align-content: center;
	opacity: 1;
	-webkit-transition: var(--transition);
	-o-transition: var(--transition);
	transition: var(--transition);
}

#curtain {
	position: fixed;
	z-index: 9;
	width: 100vw;
	height: 100vh;
	top: 0;
	left: 0;
	opacity: 1;
	-webkit-transition: opacity .6s ease-in-out;
	-o-transition: opacity .6s ease-in-out;
	transition: opacity .6s ease-in-out;
	background: rgba(0,0,0,.6);
}

.modal .modal-box {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	font-size: calc(.8em + 1vw);
	background: rgba(255,255,255,.9);
	border: 2px solid var(--lightgray);
	border-radius: var(--radius);
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	padding: 2rem;
	position: relative;
	-webkit-transition: var(--transition);
	-o-transition: var(--transition);
	transition: var(--transition);
	z-index: 9999;
}

.modal input {
	width: 100%;
	font-size: 1.4rem;
	font-family: var(--font);
	/*margin-bottom: .4em;*/
	border: 2px solid var(--lightgray);
	padding: .3em;
}

.modal button, .modal input[type=submit]{
	background: var(--blue);
	font-size: .8em;
	padding: .5em;
	color: #fff;
	font-weight: bold;
	line-height: 1.5em;
	width: auto;
	line-height: 1;
	margin-top: .5em;
	-ms-flex: 1 1 100%;
	    flex: 1 1 100%;
}

.modal label {
	margin-bottom: .2em;
}

.button-group {
	display: -ms-flexbox;
	display: flex;
	width: 100%;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.button-group > * {
	-ms-flex: 1 1 50% !important;
	    flex: 1 1 50% !important;
}

.button-group > *:not(:last-child){
	margin-right: 1em;
}

@media (max-width: 760px) {
	.modal button, .modal input[type=submit]{
		font-size: 1em;
	}
}


